@use 'sass:math';

// Define variáveis globais
$primary-color: #32ABFF;
$secondary-color: white;
$bg-color: rgb(34, 34, 34);

// Regras de estilo para todos os elementos
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; // Corrige o comportamento do box-sizing
}

// Estilos para o corpo e o cabeçalho
body,
header {
  background-color: $bg-color;
  font-family: 'Roboto', sans-serif, 'Ubuntu', sans-serif;
}

header {
  justify-content: space-between;
  width: 100%;
  left: 0;
  position: fixed;
  z-index: 1000;

  ul {
    list-style: none;
    gap: 15px;
    width: 300px;
  }
}

@mixin flex-container {
  display: flex;
}

header,
header ul,
#home,
#img-perfil,
section>h1,
#cv,
#skills-section,
#skills-container,
#projects-container .project,
#project-modal,
.project-modal-on,
.project-btns-container,
#contacts div a {
  @include flex-container;
}

#skills-section {
  flex-direction: column;
}

body,
a,
.progress,
.project-btns-container button {
  color: $secondary-color;
}

// Estilos para links (âncoras)
a {
  text-decoration: none;
}

#menu,
#menu ul,
#overlay {
  padding: 0;
}

#menu li {
  list-style: none;
  border-bottom: 1px solid $bg-color;
  transition: 0.5s;

  &:hover {
    border-bottom: 1px solid $primary-color;
  }
}

// Estilos para a barra de menu (menu)
header,
#about-me-section,
#skills-container,
#skills-section,
.project {
  align-items: center;
}

section>h1,
#cv,
.project {
  justify-content: center;
}

section>h1,
#heading {
  font-family: 'Ubuntu', sans-serif;
}

.apresentation-strings {
  color: $primary-color;
}

// Estilos para o botão de currículo (cv)
#cv {
  border: 1px solid $primary-color;
  border-radius: 10px;
  font-weight: bolder;
  cursor: pointer;
  transition: 300ms;
}

// Efeito de hover para o botão de currículo (cv)
#cv:hover,
.progressbar {
  background-color: $primary-color;
}

#img-perfil {
  border-radius: 50%;
  margin: 0 auto;
}

.project-btns-container button,
.project-btns-container,
.project {
  width: 100%;
}

.project {
  position: relative;

  width: 600px;
  /* Defina o tamanho desejado */
  height: 400px;
}

.background-project {
  width: 100%;
  height: 100%;
  object-fit: cover;
  image-rendering: crisp-edges;
}

.project div {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  /* Centraliza o texto */
}

.project:hover {
  transition: 0.5s;
  transform: scale(1.05);
}

.project-btns-container {
  position: absolute;
  bottom: 0;
}

// Estilos para o contêiner de informações de contato (contacts)
#contacts div {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

// Estilos para links dentro do contêiner de informações de contato (contacts)
#contacts div a {
  justify-content: space-evenly;
}

.project-modal-overlay-bg {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.928);
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 3;
}

#project-modal {
  flex-direction: column;
  transition: 0, 5s ease-in;
  position: fixed;
  font-size: 18px;
  margin: 0 auto;
  top: 50%;
  background-color: $bg-color;
  transform: translatey(-50%);
  z-index: 6;
}

#test-project-a {
  background-color: rgb(0, 18, 219);
}

#test-project-a:hover {
  background-color: rgb(4, 17, 163);
}

#see-code-a {
  background-color: #035114;
}

.see-code:hover {
  background-color: #099227;
}

// Media query para estilos responsivos em telas com largura máxima de 767px
@media (max-width: 767px) {

  // Ajusta o tamanho da fonte e o preenchimento para todos os elementos filhos diretos do corpo
  body>* {
    font-size: 16px;
    padding: 0 10px;
  }

  header {
    padding: 0 10px;
    h1 {
      margin: 0;
      font-size: 18px;
    }
  }

  // Define o tamanho do ícone do menu
  #menu-btn {
    font-size: 30px;
  }

  .overlay,
  #projects-container {
    @include flex-container();
  }

  .overlay {
    justify-content: center;
    position: fixed;
    background-color: rgb(37, 37, 37);
    width: 100%;
    height: 100%;
    top: 50;
    left: 0;
  }

  // Estilos para o menu quando a tela é pequena
  .menu-list {
    position: absolute;
    justify-content: center;
    transition: 0.5s;
    display: block;
    padding: 0;
    li {
      text-align: center;
      margin-top: 50px;
      a {
        font-weight: bolder;
      }
    }
  }

  .menu-list li #presentation,
  #img-perfil {
    margin-top: 50px;
  }

  // Ajusta o estilo da imagem de perfil
  #img-perfil {
    width: 120px;
  }

  section>h1 {
    font-size: 30px;
    margin: 30px 0;
  }

  #heading {
    font-size: 38px;
    letter-spacing: 3px;
    font-weight: 500;
  }

  #description {
    margin: 20px 0;
  }

  // Ajusta o preenchimento, o tamanho da fonte e o alinhamento para o botão de currículo (cv)
  #cv {
    padding: 10px;
    font-size: 20px;
  }

  .skills {
    display: block;
    margin-bottom: 40px;
    gap: 40px;
  }

  .skill-headers {
    margin-bottom: 10px;
  }

  #skills-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 15px
  }

  .skills h2 {
    margin-top: 10px;
  }

  .progress {
    height: 25px;
  }

  .project {
    margin-bottom: 10px;
    width: 100%;
    height: 50%;

    .background-project {
      filter: brightness(0.4);
    }

    .program-lang-img {
      width: 40px;
    }
  }


  #projects-container {
    flex-wrap: wrap;
  }

  #project-modal {
    padding: 10px;
  }

  #project-modal img {
    display: none;
  }

  // Ajusta o tamanho da imagem dentro do contêiner de informações de contato
  #contacts div img {
    width: 80px;
  }

  .hidden {
    display: none;
  }
}



// Media query para telas com largura mínima de 1200px
@media (min-width: 1024px) {

  // Ajusta o tamanho da fonte para o corpo
  body {
    font-size: 18px;
  }

  // Oculta o ícone do menu
  #menu-btn {
    display: none;
  }

  section>h1 {
    font-size: 45px;
    margin: 60px 0;
  }

  // Configura a seção "home" para exibir seus elementos em uma coluna
  #home {
    height: 90vh;
    width: 35%;
    margin: 0;
  }

  #heading {
    font-size: 60px;
    letter-spacing: 2px;
    font-weight: 500;
  }

  // Ajusta a margem para a descrição
  #description {
    margin: 30px 0;
  }

  // Ajusta o preenchimento e o tamanho da fonte para o botão de currículo (cv)
  #cv {
    padding: 25px;
    font-size: 30px;
  }

  // Ajusta o estilo da imagem de perfil
  #img-perfil {
    width: 200px;
  }

  .image-border {
    padding: 20px;
  }

  #skills-container {
    width: 500px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
  }

  #skills-container img {
    width: 80px;
  }

  .skill-headers {
    margin-bottom: 10px;
  }

  .skills h2 {
    margin-top: 10px;
  }

  .progress {
    height: 25px;
  }

  #projects-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
  }

  #project-modal img {
    width: 900px;
  }

  // Ajusta o tamanho da imagem dentro do contêiner de informações de contato
  #contacts div img {
    width: 130px;
  }
}